import Box from '@mui/material/Box';
import { useState } from 'react';
import { useCommon } from '../../components/hooks/useCommon';
import Utils from '../../util/Utils';
import constantes from '../../util/constantes';
import DrawerMenu from './DrawerMenu';
import MenuMobile from './MenuMobile';

export default function HeaderBar(props) {
    const { userDetails, corpo } = props
    const [exibirModal, setExibirModal] = useState(false)
    const [showMenu, setShowMenu] = useState(false)
    const { height, width } = Utils.useWindowDimensions();
    const isMobile = width < constantes.MOBILE_SCREEN_SIZE

    const handleClick = (e) => {
        setShowMenu(e.currentTarget);
    };

    const handleCloseMenu = () => {
        setShowMenu(null)
    }

    return (
        <Box sx={{ flexGrow: 1, width: '100%', bottom: 0, position: 'absolute' }}>

            {isMobile ?
                <MenuMobile
                    corpo={corpo}
                    userDetails={userDetails}
                    setExibirModal={setExibirModal}
                    handleCloseMenu={handleCloseMenu}
                    handleClick={handleClick}
                    showMenu={showMenu}
                    setShowMenu={setShowMenu}
                    isMobile={isMobile}
                />
                :
                <DrawerMenu
                    userDetails={userDetails}
                    setExibirModal={setExibirModal}
                    handleCloseMenu={handleCloseMenu}
                    handleClick={handleClick}
                    showMenu={showMenu}
                    setShowMenu={setShowMenu}
                    isMobile={isMobile}
                />

            }
        </Box >
    );
}