import { useState, Fragment } from 'react';
import Box from '@mui/material/Box';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Controls from '../../controls/Controls';

const steps = [
    'Especialidade',
    'Escolher Profissional',
    'Confirmação',
];

export default function StepperAgendamento(props) {
    const { children, activeStep, setActiveStep, fecharPopup} = props
    const [skipped, setSkipped] = useState(new Set());

    const isStepSkipped = (step) => {
        return skipped.has(step);
    };

    const avancarStep = () => {
        let newSkipped = skipped;
        if (isStepSkipped(activeStep)) {
            newSkipped = new Set(newSkipped.values());
            newSkipped.delete(activeStep);
        }

        setActiveStep((prevActiveStep) => prevActiveStep + 1);
        setSkipped(newSkipped);
    }

    const handleNext = async () => {
        avancarStep()
    };

    const handleBack = () => {
        setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };

    const handleFechar = () => {
        fecharPopup(false)
    };


    return (
        <Box sx={{ width: '100%', paddingTop: '20px', paddingBottom: '20px' }}>
            <Stepper activeStep={activeStep} alternativeLabel>
                {steps.map((label, index) => {
                    const stepProps = {};
                    const labelProps = {};
                    if (isStepSkipped(index)) {
                        stepProps.completed = false;
                    }
                    return (
                        <Step key={label} {...stepProps}>
                            <StepLabel {...labelProps}>{label}</StepLabel>
                        </Step>
                    );
                })}
            </Stepper>
            {children}
            {activeStep === steps.length ? (
                <Fragment>
                    <Typography sx={{ mt: 2, mb: 1 }}>
                        Agendamento concluído! Aguarde a confirmação do seu agendamento.
                    </Typography>
                    <Box sx={{ display: 'flex', flexDirection: 'row', pt: 2 }}>
                        <Box sx={{ flex: '1 1 auto' }} />
                        <Button onClick={() => handleFechar()}>Fechar</Button>
                    </Box>
                </Fragment>
            ) : (
                <Fragment>
                    <Box sx={{ display: 'flex', marginTop: '25px', marginInline: '5px' }}>
                        <Controls.Button
                            text='Anterior'
                            size='large'
                            onClick={handleBack}
                            disabled={activeStep === 0}
                        />

                        <Box sx={{ flex: '1 1 auto' }} />
                        {activeStep === steps.length - 1 ?
                            <Box>
                                <Controls.Button
                                    text={'Finalizar'}
                                    size='large'
                                    onClick={handleNext}
                                />
                            </Box>
                            :
                            <Controls.Button
                                text={'Próximo'}
                                size='large'
                                onClick={handleNext}
                            />
                        }
                    </Box>
                </Fragment>
            )}


        </Box>
    );
}