import { Box } from "@mui/material";
import DiasSemana from "./tabela/DiasSemana";
import * as Cores from '../../../../util/cores'
import DiasMes from "./tabela/DiasMes";


export default function TabelaDias(props) {
    const { selecionarDiaMes, diaMesSelecionado } = props

    return (
        <Box>
            <DiasSemana />
            <DiasMes
                selecionarDiaMes={selecionarDiaMes}
                diaMesSelecionado={diaMesSelecionado}
            />
        </Box>
    )
}