import { Box, Typography } from "@mui/material";
import * as Cores from '../../../../util/cores'
import * as Constantes from '../../../../util/constantes'
import CalendarMonthIcon from '@mui/icons-material/CalendarMonth';
import EventAvailableIcon from '@mui/icons-material/EventAvailable';

export default function DataSelecionadaHeader(props) {

    const { alterarCalendarioAgendamento, calendarioAgendamento } = props

    return (
        <Box sx={{display: 'flex', justifyContent: 'space-around' }}>
            <Box sx={{ padding: '5px', borderRadius: '20px', width: '100%', background: calendarioAgendamento == Constantes.CALENDARIO ? Cores.COR_PRINCIPAL_FOSCO : 'white', paddingInline: '8px' }}
                onClick={() => alterarCalendarioAgendamento(Constantes.CALENDARIO)}>
                <CalendarMonthIcon sx={{ fill: calendarioAgendamento == Constantes.CALENDARIO ? 'white' : Cores.COR_SECUNDARIA }} />
                <Typography color={calendarioAgendamento == Constantes.CALENDARIO ? 'white' : Cores.COR_SECUNDARIA}>
                    {Constantes.CALENDARIO}
                </Typography>

            </Box>
            <Box sx={{ padding: '5px', borderRadius: '20px', width: '100%', background: calendarioAgendamento == Constantes.AGENDAMENTO ? Cores.COR_PRINCIPAL_FOSCO : 'white', paddingInline: '8px' }}
                onClick={() => alterarCalendarioAgendamento(calendarioAgendamento == Constantes.AGENDAMENTO ? 'white' : Constantes.AGENDAMENTO)}>
                <EventAvailableIcon sx={{ fill: calendarioAgendamento == Constantes.AGENDAMENTO ? 'white' : Cores.COR_SECUNDARIA }} />
                <Typography color={calendarioAgendamento == Constantes.AGENDAMENTO ? 'white' : Cores.COR_SECUNDARIA}>
                    {Constantes.AGENDAMENTO}
                </Typography>
            </Box>
        </Box>
    )
}