
import VisibilityIcon from '@mui/icons-material/Visibility';
import { Box, Grid, IconButton, InputAdornment, Typography } from "@mui/material";
import { useState } from 'react';
import ClienteService from "../../../api/services/ClienteService";
import { Form, useForm } from "../../../components/UseForm";
import Controls from "../../../components/controls/Controls";
import NewTabButton from "../../../components/controls/NewTabButton";
import Mensagens from "../../../util/Mensagens";
import * as Cores from '../../../util/cores'

const initialValues = {
    id: 0,
    username: '',
    email: '',
    senha: '',
    confirmarSenha: '',
    check: false
}

export default function CadastroClinica(props) {
    const { alterarLoad, notificar, alterarSelecao } = props
    const [mostraSenha, setMostraSenha] = useState('password')

    const validate = (fieldValues = values) => {
        let temp = { ...erros }
        if ('email' in fieldValues) {
            temp.email = fieldValues.email !== '' ? '' : Mensagens.CAMPO_OBRIGATORIO
            if (temp.email !== Mensagens.CAMPO_OBRIGATORIO) {
                temp.email = (/^$|.+@.+..+/).test(fieldValues.email) ? '' : Mensagens.CAMPO_INVALIDO
            }
        }
        if ('senha' in fieldValues) {
            temp.senha = fieldValues.senha.length >= 8 ? '' : Mensagens.CAMPO_OBRIGATORIO
        }
        if ('confirmarSenha' in fieldValues) {
            temp.confirmarSenha = fieldValues.confirmarSenha.length >= 8 ? '' : Mensagens.CAMPO_OBRIGATORIO
            if (temp.confirmarSenha !== Mensagens.CAMPO_OBRIGATORIO) {
                temp.confirmarSenha = fieldValues.confirmarSenha === values.senha ? '' : Mensagens.SENHAS_DIFEREM
            }
        }
        if ('username' in fieldValues) {
            temp.username = fieldValues.username.length >= 4 ? '' : Mensagens.CAMPO_OBRIGATORIO
        }
        if ('check' in fieldValues) {
            temp.check = fieldValues.check ? '' : Mensagens.TERMOS_DEVEM_SER_ACEITOS
        }
        setErros({
            ...temp
        })

        if (fieldValues === values)
            return Object.values(temp).every(x => x === "")
    }

    const {
        values,
        setValues,
        handleInputChange,
        erros,
        setErros
    } = useForm(initialValues, true, validate)

    async function cadastrar() {
        alterarLoad(true)
        try {
            await ClienteService.cadastrarCliente({
                userTO: {
                    username: values.username,
                    password: values.senha,
                },
                isAlterar: false,
                id: 0,
                email: values.email
            }).then(response => {
                alterarLoad(false)
                if (response.status !== 200) {
                    notificar(Mensagens.ERRO_SERVICO, "error")
                } else if (!response.data.erro) {
                    notificar(Mensagens.CADASTRO_SUCESSO, "success")
                } else {
                    notificar(Mensagens.CADASTRO_ERRO + response.data.mensagem, "error")
                }
            })
        } catch (e) {
            alterarLoad(false)
        }

    }

    const handleSubmit = e => {
        e.preventDefault()
        if (validate()) {
            cadastrar()
        }

    }

    const handleClickShowPassword = () => {
        if (mostraSenha === 'password') {
            setMostraSenha('text')
        } else {
            setMostraSenha('password')
        }
    }
    return (
        <Box sx={{background: Cores.COR_PRINCIPAL, borderTopRightRadius: '25px', borderTopLeftRadius: '25px', height: '100%'}}>
            <Typography color='white' fontWeight={700}>Cadastrar-se como Profissional ou Clínica</Typography>
            <Form onSubmit={handleSubmit}>
                <Grid container spacing={3}>
                    <Grid item md={12} xs={12}>
                        <Controls.TextField
                            name='username'
                            label='Usuário'
                            onChange={handleInputChange}
                            value={values.username}
                            error={erros.username}
                        />
                    </Grid>
                    <Grid item md={12} xs={12}>
                        <Controls.TextField
                            name='email'
                            label='Email'
                            onChange={handleInputChange}
                            value={values.email}
                            error={erros.email}
                        />
                    </Grid>
                    <Grid item md={12} xs={12}>
                        <Controls.TextField
                            name='senha'
                            label='Senha'
                            onChange={handleInputChange}
                            value={values.senha}
                            error={erros.senha}
                            type={mostraSenha}
                            InputProps={{
                                disableUnderline: true,
                                endAdornment: (
                                    <InputAdornment position="end">
                                        <IconButton
                                            aria-label="toggle password visibility"
                                            onClick={handleClickShowPassword}
                                        >
                                            <VisibilityIcon />
                                        </IconButton>
                                    </InputAdornment>
                                )
                            }}
                        />
                    </Grid>
                    <Grid item md={12} xs={12}>
                        <Controls.TextField
                            name='confirmarSenha'
                            label='Confirmar Senha'
                            onChange={handleInputChange}
                            value={values.confirmarSenha}
                            error={erros.confirmarSenha}
                            type={mostraSenha}
                            InputProps={{
                                disableUnderline: true,
                                endAdornment: (
                                    <InputAdornment position="end">
                                        <IconButton
                                            aria-label="toggle password visibility"
                                            onClick={handleClickShowPassword}
                                        >
                                            <VisibilityIcon />
                                        </IconButton>
                                    </InputAdornment>
                                )
                            }}
                        />
                    </Grid>
                    <Box sx={{ display: 'flex', paddingTop: '4%', marginLeft: '5%', width: '100%' }}>
                        <NewTabButton link="/termos" texto="Termos de Uso" />
                        <Box sx={{ paddingInline: '4%' }} />
                        <NewTabButton link="/politica" texto="Política de Privacidade" />
                    </Box>
                    <Grid item md={12} xs={12}>
                        <Controls.Checkbox
                            fontColor='white'
                            name="check"
                            label="Li e aceito os termos de uso e a política de privacidade da Health Connect."
                            value={values.check}
                            error={erros.check}
                            onChange={handleInputChange}
                        />
                    </Grid>
                    <Grid item md={6} xs={6}>
                        <Controls.Button
                            sx={{background: 'white', color: Cores.COR_PRINCIPAL, fontWeight: 700}}
                            text='Cadastrar'
                            type='submit'
                        />
                    </Grid>
                    <Grid item md={6} xs={6}>
                        <Controls.Button
                            sx={{background: 'white', color: Cores.COR_PRINCIPAL, fontWeight: 700}}
                            text='Voltar'
                            onClick={() => alterarSelecao('')}
                        />
                    </Grid>
                </Grid>
            </Form>
        </Box>

    )
}