import { Box, Grid, Typography } from "@mui/material"
import Controls from "../../controls/Controls"
import { Form, useForm } from "../../UseForm"

const initialValues = {
    especialidade: 0,
    local: 0,
    tipoAtendimento: 1,
    convenio: 0,
    numeroCarteirinha: '',
    plano: '',
    endereco: 'Av. Santos Dumont 782 - CENTRO - FORTALEZA'
}

const listaLocal = [{ id: 0, value: 'Selecione' }, { id: 1, value: 'CLINICAMED FORTALEZA/CE (SEDE)' }]
const listaTipoAtendimento = [{ id: 1, value: 'Presencial' }, { id: 2, value: 'Online' }]
const listaConvenio = [{ id: 0, value: 'Selecione' }, { id: 1, value: 'CAMED' }, { id: 2, value: 'UNIMED' }]

export default function Especialidade(props) {
    const { adicionarEspecialidade } = props

    const validate = () => {

    }

    const {
        values,
        setValues,
        handleInputChange,
        erros,
        setErros
    } = useForm(initialValues, true, validate)

    const handleSubmit = () => {

    }


    return (
        <Box>
            <Form onSubmit={handleSubmit} >
                <Grid container spacing={3} >
                    <Grid item md={12} xs={12}>
                        <Controls.Input.Especialidade
                            name='especialidade'
                            value={values.especialidade}
                            error={erros.especialidade}
                            onChange={handleInputChange}
                        />
                    </Grid>
                    <Grid item md={12} xs={12}>
                        <Controls.Select
                            name='local'
                            label='Local'
                            value={values.local}
                            error={erros.local}
                            options={listaLocal}
                            onChange={handleInputChange}
                        />
                    </Grid>
                    {values.local !== 0 && <Grid item md={12} xs={12}>
                        <Controls.CampoSemInput
                            label='Endereço'
                            texto={values.endereco}
                        />
                    </Grid>}
                    <Grid item md={12} xs={12}>
                        <Controls.Select
                            name='tipoAtendimento'
                            label='Tipo Atendimento'
                            value={values.tipoAtendimento}
                            error={erros.tipoAtendimento}
                            options={listaTipoAtendimento}
                            onChange={handleInputChange}
                        />
                    </Grid>
                    <Grid item md={12} xs={12}>
                        <Controls.TextField
                            name='numeroCarteirinha'
                            label='Nº Carteirinha'
                            value={values.numeroCarteirinha}
                            error={erros.numeroCarteirinha}
                            onChange={handleInputChange}
                        />
                    </Grid>
                    <Grid item md={12} xs={12}>
                        <Controls.Select
                            name='convenio'
                            label='Convênio'
                            value={values.convenio}
                            error={erros.convenio}
                            options={listaConvenio}
                            onChange={handleInputChange}
                        />
                    </Grid>
                    <Grid item md={12} xs={12}>
                        <Controls.TextField
                            name='plano'
                            label='Plano'
                            value={values.plano}
                            error={erros.plano}
                            onChange={handleInputChange}
                        />
                    </Grid>
                </Grid >
            </Form>
        </Box>
    )
}