import { Table, TableCell, TableHead, TablePagination, TableRow } from "@material-ui/core"
import { Box, TableSortLabel, Typography } from "@mui/material"
import { useState } from "react"
import * as Cores from "../util/cores"

export default function UseTable(records, headCells, filterFn) {

    const pages = [10, 20, 30]
    const [page, setPage] = useState(0)
    const [rowsPerPage, setRowsPerPage] = useState(pages[page])
    const [order, setOrder] = useState()
    const [orderBy, setOrderBy] = useState()

    const TblContainer = props => (
        <Table>
            {props.children}
        </Table>
    )

    const TblHead = props => {
        const handleSortRequest = cellId => {
            const isAsc = orderBy === cellId && order === 'asc'
            setOrder(isAsc ? 'desc' : 'asc')
            setOrderBy(cellId)
        }

        return (
            <TableHead style={{ backgroundColor: Cores.COR_SECUNDARIA }}>
                <TableRow>
                    {
                        headCells.map(headCell => (
                            <TableCell key={headCell.id} sx={{ fontWeight: 700 }}
                                sortDirection={orderBy === headCell.id ? order : false}>
                                {headCell.disableSorting ?
                                    <Typography color='white' fontWeight={700}>{headCell.label}</Typography>
                                    :
                                    <TableSortLabel
                                        active={orderBy === headCell.id}
                                        direction={orderBy === headCell.id ? order : 'asc'}
                                        onClick={() => { handleSortRequest(headCell.id) }}>
                                        <Typography color='white' fontWeight={700}>{headCell.label}</Typography>
                                    </TableSortLabel>}
                            </TableCell>
                        ))
                    }
                </TableRow>
            </TableHead>
        )
    }

    const handleChangePage = (event, newPage) => {
        setPage(newPage)
    }

    const handleChangeRowsPerPage = event => {
        setRowsPerPage(parseInt(event.target.value))
        setPage(0)
    }

    const labelDisplayedRows = ({ from, to, count }) => {
        return `${from}–${to} de ${count !== -1 ? count : `mais que ${to}`}`;
    }

    const TblPagination = () => (
        <Box>
          
        </Box>)

    function stableSort(array, comparator) {
        const stabilizedThis = array.map((el, index) => [el, index])
        stabilizedThis.sort((a, b) => {
            const order = comparator(a[0], b[0])
            if (order !== 0) return order
            return a[1] = b[1]
        })

        return stabilizedThis.map((el) => el[0])
    }

    function getComparator(order, orderBy) {
        return order === 'desc'
            ? (a, b) => descendingComparator(a, b, orderBy)
            : (a, b) => -descendingComparator(a, b, orderBy)
    }

    function descendingComparator(a, b, orderBy) {
        if (b[orderBy] < a[orderBy]) {
            return -1
        }
        if (b[orderBy] > a[orderBy]) {
            return 1
        }
        return 0

    }

    const recordsAfterPagingAndSorting = () => {
        return stableSort(filterFn.fn(records), getComparator(order, orderBy))
            .slice(page * rowsPerPage, (page + 1) * rowsPerPage)
    }

    return {
        TblContainer,
        TblHead,
        TblPagination,
        recordsAfterPagingAndSorting
    }
}