import { Box, IconButton, Typography } from "@mui/material";
import { Link } from "react-router-dom";
import * as Cores from '../../util/cores'

export default function CardMinhaArea(props) {
    const { icone, link, titulo } = props
    return (
        <Link to={link} style={{ textDecoration: 'none', color: 'white' }}>
            <Box sx={{
                background: Cores.COR_PRINCIPAL_FOSCO, borderRadius: '15px', width: '97%', height: '100%', margin: '10px',
                boxShadow: '0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)'
            }}>
                <IconButton sx={{
                    boxShadow: '0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)',
                    marginTop: '13%',
                    background: 'white',
                    '& .MuiSvgIcon-root': {
                        fill: Cores.COR_PRINCIPAL_FOSCO,
                        
                    }
                }} component="label">
                    {icone}
                </IconButton>
                <Typography sx={{ fontWeight: 700, transform: 'translate(0px, 25%)' }} >{titulo}</Typography>
            </Box>
        </Link>
    )
}