import { Box, Container } from '@mui/material';
import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import HeaderBar from '../components/header/HeaderBar';
import constantes from '../util/constantes';
import * as Constantes from '../util/constantes';
import * as Cores from '../util/cores';
import Wrapper from "./Wrapper";

function Page(props) {
    const { corpo, userDetails, setUserDetails, invalidarUsuarioLogado } = props
    const navigate = useNavigate();
    console.log(corpo)
    useEffect(() => {
        if ((userDetails === null || userDetails.id === 0) && corpo !== Constantes.PAGE_ALTERAR_SENHA && corpo !== constantes.PAGE_ATIVAR_CONTA
            && corpo !== Constantes.PAGE_LOGIN && corpo !== Constantes.PAGE_AGENDAMENTO) {
            navigate('/', { replace: true });
        }
    }, []);

    return (
        <Box style={{
            position: 'absolute', top: 0, left: 0, width: '100%', height: '100%', zIndex: '-1',
            background: 'linear-gradient(0deg, rgba(17,79,237,1) 31%, rgba(53,214,139,0.9136029411764706) 84%, rgba(25,182,106,0.958420868347339) 100%)'
        }}>
            
            <Box  sx={{height: '100%'}}>
                <Wrapper corpo={corpo}
                    userDetails={userDetails}
                    setUserDetails={setUserDetails}
                    invalidarUsuarioLogado={invalidarUsuarioLogado}
                />
            </Box>
            <Box sx={{bottom: 0}}>
                <HeaderBar
                    corpo={corpo}
                    setUserDetails={setUserDetails}
                    userDetails={userDetails}
                />
            </Box>
        </Box >
    )
}

export default Page;
