import { Box, Divider, Typography } from "@mui/material";
import * as Cores from '../../../../util/cores';

const horas = ['01:00', '02:00', '03:00', '04:00', '05:00', '06:00', '07:00', '08:00', '09:00', '10:00', '11:00', '12:00', '13:00', '14:00', '15:00', '16:00', '17:00', '18:00',
    '19:00', '20:00', '21:00', '22:00', '23:00', '24:00']

export default function InformacoesDia(props) {
    const { isMobile, dataSelecionada, abrirDrawerInformacoes, horaSelecionada } = props

    return (
        <Box >
            <Divider />
            <Typography sx={{ padding: '5px', background: Cores.COR_SECUNDARIA_FUNDO }} textAlign='left' fontSize={'large'}
                color={Cores.COR_SECUNDARIA}>Agenda Diária - 02 Fevereiro 2024</Typography>
            <Box sx={{ overflowY: 'scroll' }}>
                {horas.map((hora, index) => {
                    return (
                        <Box key={index} sx={{ background: horaSelecionada == hora ? Cores.COR_PRINCIPAL_FUNDO : '', height: '100px' }} onClick={(e) => abrirDrawerInformacoes(e, hora)}>
                            <Typography sx={{ padding: '4px' }} fontSize={'small'} textAlign={'left'} color={Cores.COR_SECUNDARIA}>{hora}</Typography>
                            <Divider />
                            {hora === '01:00' && <Box sx={{
                                width: '15%', height: '100%', background: Cores.COR_FUNDO_GRADIENTE2, borderRadius: '10px',
                                marginInline: '5px', marginLeft: '50px'
                            }} />}
                            {hora === '02:00' &&
                                <Box sx={{ marginLeft: '50px', height: '100%', display: 'flex' }}>
                                    <Box sx={{ width: '15%', height: '50%', background: Cores.COR_FUNDO_GRADIENTE2, borderRadius: '10px', marginInline: '5px', marginTop: '4%' }} />
                                    <Box sx={{ width: '15%', height: '75%', background: Cores.COR_FUNDO_GRADIENTE2, borderRadius: '10px', marginInline: '5px', marginTop: '4%' }} />
                                </Box>}
                        </Box>
                    )
                })}
            </Box>
        </ Box>
    )
}