import { Box, Grid, Paper, Typography } from "@mui/material";
import { useEffect, useState } from 'react';
import { useSearchParams } from "react-router-dom";
import Load from "../../components/Load";
import Notificacao from "../../components/Notificacao";
import { Form } from "../../components/UseForm";
import Controls from "../../components/controls/Controls";
import ClienteService from "../../api/services/ClienteService";
import Mensagens from "../../util/Mensagens";

export default function AtivarConta(props) {
    const { userDetails } = props
    const [showPage, setShowPage] = useState(false)
    const [showLoad, setShowLoad] = useState(false)
    const [params] = useSearchParams();
    const [mostraSenha, setMostraSenha] = useState('password')
    const [notify, setNotify] = useState({
        isOpen: false,
        message: "",
        type: "",
    })

    useEffect(() => {
        checarLink(params.get("hash"))
    }, []);

    async function checarLink(hash) {
        setShowLoad(true)
        try {
            await ClienteService.checarLink(hash).then(response => {
                setShowLoad(false)
                if (response.status !== 200) {
                    setShowPage(false)
                    setNotify({
                        isOpen: true,
                        message: Mensagens.ERRO_SERVICO,
                        type: "error",
                    })
                } else if (response.id === -1) {
                    setShowPage(false)
                    setNotify({
                        isOpen: true,
                        message: response.mensagem,
                        type: "error",
                    })
                } else {
                    setShowPage(true)
                }
            })
        } catch (e) {
            setShowPage(false)
            setShowLoad(false)
        }
    }

    async function ativarConta() {
        setShowLoad(true)
        try {
            await ClienteService.ativarConta({ link: params.get("hash"), email: params.get("email"), username: params.get("username") }).then(response => {
                setNotify({
                    isOpen: true,
                    message: response.erro ? Mensagens.CONTA_ATIVADA_ERRO : Mensagens.CONTA_ATIVADA_SUCESSO,
                    type: response.erro ? 'erro' : "success",
                })
            })
        } catch (e) {
            setNotify({
                isOpen: true,
                message: Mensagens.ERRO_SERVICO,
                type: 'erro',
            })
        } finally {
            setShowLoad(false)
        }
    }

    const handleSubmit = e => {
        e.preventDefault()
        ativarConta()
    }

    return (
        <Paper sx={{}}>
            {showPage ?
                <Box sx={{ paddingTop: '4%', display: 'flex', justifyContent: 'center', paddingBottom: '19%' }}>
                    <Form onSubmit={handleSubmit} >
                        <Notificacao notify={notify} setNotify={setNotify} />
                        <Load exibirLoad={showLoad} mensagemLoad='Alterando senha...' />
                        <Grid container spacing={3} sx={{ width: '500px' }} >
                            <Grid item md={12} sx={{ paddingBottom: '4%', }}>
                                {Mensagens.OLA_ATIVAR_CONTA(params.get("username"))}
                            </Grid>
                            <Grid item md={12}>
                                <Controls.Button
                                    text='Ativar Conta'
                                    type='submit'
                                />
                            </Grid>
                        </Grid>
                    </Form>
                </Box>
                :
                <Typography sx={{ padding: '22%' }}>{Mensagens.LINK_INDISPONIVEL}</Typography>
            }

        </Paper>
    )
}