import Controls from "../Controls";

const listaProfissional = [{ id: 0, value: 'Todos' }, { id: 1, value: 'João Médico' }, { id: 2, value: 'Sidney Médico' }]

export default function Profissional(props) {

    const { name, value, error = null, onChange, sx, ...other } = props;

    return (
        <Controls.Select
            error={error}
            name={name}
            value={value}
            label='Profissional'
            options={listaProfissional}
            onChange={onChange}
        />
    )
}

