import { Box, Typography } from "@mui/material";
import * as Cores from '../../../../../util/cores';


export default function SelecionarDiaMesAno(props) {
    const {selecionarSemanaMesAno, periodo, semanaMesAnoSelecionado} = props

    return (
        <Box onClick={() => selecionarSemanaMesAno(periodo)} sx={{ borderRadius: '25px', 
            width: '100%', background: semanaMesAnoSelecionado === periodo ? Cores.COR_PRINCIPAL_FOSCO : Cores.COR_PRINCIPAL_FUNDO}}>
            <Typography color={semanaMesAnoSelecionado === periodo ? 'white' : ''}>{periodo}</Typography>
        </Box>
    )
}