import { Box, TableBody, TableCell, TableRow } from "@mui/material";
import UseTable from '../../../../components/UseTable'
import { useState } from 'react'
import DeleteIcon from '@mui/icons-material/Delete';
import VisibilityIcon from '@mui/icons-material/Visibility';
import Controls from "../../../../components/controls/Controls";

const headCells = [
    { id: 'tipo', label: 'Tipo' },
    { id: 'procedimento', label: 'Procedimento' },
    { id: 'data', label: 'Data' },
    { id: 'status', label: 'Status' },
    { id: 'acao', label: 'Ação', disableSorting: true }
]

const dadosConsulta = [{ id: 1, tipo: 'Consulta', procedimento: 'Consulta Pediatra', data: '27/03/2024 10:15', status: 'Pendente Confirmação' }]

export default function Tabela(props) {
    const {abrirPopupVisualizar, abrirPopupCancelar} = props
    const [records, setRecords] = useState(dadosConsulta)
    const [filterFn, setFilterFn] = useState({ fn: items => { return items } })

    const {
        TblContainer,
        TblHead,
        TblPagination,
        recordsAfterPagingAndSorting
    } = UseTable(records, headCells, filterFn);

    return (
        <Box sx={{paddingLeft: '2px'}}>
            <TblContainer >
                <TblHead />
                <TableBody sx={{ background: 'white', }}>
                    {recordsAfterPagingAndSorting().map((item, index) => (
                        <TableRow key={index}>
                            <TableCell>{item.tipo}</TableCell>
                            <TableCell>{item.procedimento}</TableCell>
                            <TableCell>{item.data}</TableCell>
                            <TableCell>{item.status}</TableCell>
                            <TableCell>
                                <Box sx={{ display: 'flex' }}>
                                    <Controls.ActionButton
                                        color='secondary'
                                        onClick={() => { abrirPopupCancelar()}}>
                                        <DeleteIcon />
                                    </Controls.ActionButton>
                                    <Controls.ActionButton
                                        color='secondary'
                                        onClick={() => {abrirPopupVisualizar() }}>
                                        <VisibilityIcon />
                                    </Controls.ActionButton>
                                </Box>
                            </TableCell>
                        </TableRow>
                    )
                    )}
                </TableBody>
            </TblContainer>
            <TblPagination />
        </Box>
    )
}