import { Box, CssBaseline, ThemeProvider } from "@mui/material";
import { createTheme } from "@mui/material/styles";
import { useEffect, useState } from 'react';
import { Helmet } from "react-helmet";
import {
	BrowserRouter,
	Route,
	Routes
} from "react-router-dom";
import secureLocalStorage from "react-secure-storage";
import './App.css';
import Page from './page/Page';
import constantes from "./util/constantes";
import * as Constantes from "./util/constantes";
import * as Cores from "./util/cores";

const preto = 'black'
const branco = 'white'

const temaBase = createTheme({

	palette: {
		primary: {
			light: Cores.COR_PRINCIPAL_CLARO,
			main: Cores.COR_PRINCIPAL_FOSCO,
			dark: Cores.COR_PRINCIPAL_ESCURO,
		},
		secondary: {
			light: Cores.COR_SECUNDARIA_CLARO,
			main: Cores.COR_SECUNDARIA,
			dark: Cores.COR_SECUNDARIA_ESCURO,
		},
	},
	components: {
		MuiButton: {
			styleOverrides: {
				root: {
					borderRadius: '30px',
					fontSize: '14px',
					textTransform: 'unset',
				}
			}
		},
		MuiPaper: {
			styleOverrides: {
				root: {
					"::-webkit-scrollbar": {
						width: "3px",
					},
					"::-webkit-scrollbar-track": {
						backgroundColor: '#fff5f5'
					},
					"::-webkit-scrollbar-thumb": {
						background: Cores.COR_SECUNDARIA,
						borderRadius: "15px",
						height: "2px"
					},
					"::-webkit-scrollbar-thumb:hover": {
						background: Cores.COR_SECUNDARIA,
						maxHeight: "10px"
					},
				}
			}
		},
		MuiFormControl: {
			styleOverrides: {
				root: {
					textAlign: 'left',
					width: '100%',
				}
			}
		},
		MuiToggleButton: {
			styleOverrides: {
				root: {
					backgroundColor: Cores.COR_SECUNDARIA,
					"&.Mui-selected": {
						color: preto,
						backgroundColor: Cores.COR_SECUNDARIA_CLARO,
						"&:hover": {
							color: branco,
							backgroundColor: Cores.COR_SECUNDARIA_CLARO
						}
					},
					"&:hover": {
						color: branco,
						backgroundColor: Cores.COR_SECUNDARIA
					}
				}
			}
		}
	}
});


const initialValues = {
	roles: constantes.ROLE_ADMIN,
	id: 0,
	username: '',
	email: '',
	senha: '',
	token: '',
	data: new Date().getTime()
}

function App() {
	const [userDetails, setUserDetails] = useState(secureLocalStorage.getItem('userDetails'));

	useEffect(() => {
		secureLocalStorage.setItem('userDetails', { ...userDetails, data: new Date().getTime() })
	}, [userDetails]);

	const invalidarUsuarioLogado = () => {
		setUserDetails(initialValues)
	}

	useEffect(() => {
		let user = userDetails
		let dataExpira
		if (user !== undefined && user !== null) {
			dataExpira = new Date(user.data + constantes.TIME_EXPIRAR_LOGIN)
		}
		console.log('dataExpira ' + dataExpira)
		if (user === null || user === undefined) {
			invalidarUsuarioLogado()
		} else if (new Date() > dataExpira) {
			invalidarUsuarioLogado()
		} else if (user.id > 0) {
			setUserDetails(user)
		} else {
			invalidarUsuarioLogado()
		}
	}, []);

	return (
		<Box className="App" >
			<Helmet>
				<title>Health connect</title>
				<link rel="canonical" href="https://kenkau.com" />
				<meta name="description" content="Gerencie suas consultas." />
				<meta name="keywords" content="Consulta, médico" />
				<meta name="author" content="Health connect" />
				<meta name="theme-color" content={Cores.COR_PRINCIPAL} />
			</Helmet>
			<ThemeProvider theme={temaBase}>
				<CssBaseline />
				<BrowserRouter>
					<Routes>
						<Route path="/" element={<Page
							setUserDetails={setUserDetails}
							invalidarUsuarioLogado={invalidarUsuarioLogado}
							userDetails={userDetails}
							corpo={Constantes.PAGE_HOME} />} />

						<Route path={"/" + Constantes.PAGE_DASHBOARD} element={<Page
							setUserDetails={setUserDetails}
							invalidarUsuarioLogado={invalidarUsuarioLogado}
							userDetails={userDetails}
							corpo={Constantes.PAGE_DASHBOARD} />} />

						<Route path={"/" + Constantes.PAGE_LOGIN} element={<Page
							setUserDetails={setUserDetails}
							invalidarUsuarioLogado={invalidarUsuarioLogado}
							userDetails={userDetails}
							corpo={Constantes.PAGE_LOGIN} />} />

						<Route path={"/" + Constantes.PAGE_PERFIL} element={<Page
							setUserDetails={setUserDetails}
							userDetails={userDetails}
							invalidarUsuarioLogado={invalidarUsuarioLogado}
							corpo={Constantes.PAGE_PERFIL} />} />

						<Route path={"/" + Constantes.PAGE_ALTERAR_SENHA} element={<Page
							setUserDetails={setUserDetails}
							userDetails={userDetails}
							invalidarUsuarioLogado={invalidarUsuarioLogado}
							corpo={Constantes.PAGE_ALTERAR_SENHA} />} />

						<Route path={"/" + Constantes.PAGE_ATIVAR_CONTA} element={<Page
							setUserDetails={setUserDetails}
							userDetails={userDetails}
							invalidarUsuarioLogado={invalidarUsuarioLogado}
							corpo={Constantes.PAGE_ATIVAR_CONTA} />} />

						<Route path={"/" + Constantes.PAGE_AGENDAMENTO} element={<Page
							setUserDetails={setUserDetails}
							userDetails={userDetails}
							invalidarUsuarioLogado={invalidarUsuarioLogado}
							corpo={Constantes.PAGE_AGENDAMENTO} />} />

						<Route path={"/" + Constantes.PAGE_PROFISSIONAIS} element={<Page
							setUserDetails={setUserDetails}
							userDetails={userDetails}
							invalidarUsuarioLogado={invalidarUsuarioLogado}
							corpo={Constantes.PAGE_PROFISSIONAIS} />} />

						<Route path={"/" + Constantes.PAGE_FINANCEIRO} element={<Page
							setUserDetails={setUserDetails}
							userDetails={userDetails}
							invalidarUsuarioLogado={invalidarUsuarioLogado}
							corpo={Constantes.PAGE_FINANCEIRO} />} />

						<Route path={"/" + Constantes.PAGE_CLINICA} element={<Page
							setUserDetails={setUserDetails}
							userDetails={userDetails}
							invalidarUsuarioLogado={invalidarUsuarioLogado}
							corpo={Constantes.PAGE_CLINICA} />} />
					</Routes>
				</BrowserRouter>
			</ThemeProvider>
		</Box>
	);
}

export default App;
