import { Box, Grid } from "@mui/material";
import { Form, useForm } from "../../components/UseForm";
import Controls from "../../components/controls/Controls";
import * as Cores from '../../util/cores'

const initialValues = {
    especialidade: 0,
    profissional: 0,
    data: ''
}

export default function Pesquisar(props) {

    const validate = () => {

    }

    const {
        values,
        setValues,
        handleInputChange,
        erros,
        setErros
    } = useForm(initialValues, true, validate)

    const handleSubmit = () => {

    }

    return (
        <Box sx={{ padding: '15px', background: 'white', borderRadius: '25px' }}>
            <Form onSubmit={handleSubmit} >
                <Grid container spacing={1} >
                    <Grid item md={12} xs={12}>
                        <Controls.Input.Especialidade
                            name='especialidade'
                            value={values.especialidade}
                            error={erros.especialidade}
                            onChange={handleInputChange}
                        />
                    </Grid>
                    <Grid item md={12} xs={12}>
                        <Controls.Input.Profissional
                            name='profissional'
                            value={values.profissional}
                            error={erros.profissional}
                            onChange={handleInputChange}
                        />
                    </Grid>
                    <Grid item md={12} xs={12}>
                        <Controls.TextField
                            name='data'
                            label='Data'
                            value={values.data}
                            error={erros.data}
                            onChange={handleInputChange}
                        />
                    </Grid>
                </Grid >
            </Form>
        </Box>
    )
}