import { Dashboard } from "@mui/icons-material";
import DateRangeIcon from '@mui/icons-material/DateRange';
import AboutUsIcon from '@mui/icons-material/Groups';
import HomeIcon from '@mui/icons-material/Home';
import LoginIcon from '@mui/icons-material/Login';
import LogoutIcon from '@mui/icons-material/Logout';
import { Drawer, IconButton, MenuItem, Typography } from '@mui/material';
import Box from '@mui/material/Box';
import { Link } from 'react-router-dom';
import * as Cores from '../../util/cores';
import DashboardIcon from '@mui/icons-material/Dashboard';
import SearchIcon from '@mui/icons-material/Search';

export default function DrawerMenu(props) {

    const { setExibirModal, showMenu, handleCloseMenu, setShowMenu, isMobile } = props

    const toggleDrawer = (e, open) => {
        e.preventDefault()
        e.stopPropagation()
        setShowMenu(open)
    }

    return (
        <Drawer
            anchor={'left'}
            open={showMenu}
            PaperProps={{
                sx: { width: '11%', borderTopRightRadius: '20px', borderBottomRightRadius: '20px', background: Cores.COR_SECUNDARIA_FUNDO },
            }}
            onClose={e => { toggleDrawer(e, false) }}
        >
            <Link to='/' style={{ textDecoration: 'none', color: Cores.COR_SECUNDARIA_ESCURO }}>
                <MenuItem onClick={handleCloseMenu}>
                    <Box sx={{ display: 'flex' }}>
                        <IconButton color="secondary" component="label">
                            <SearchIcon fontSize='large' />
                        </IconButton>
                        <Typography sx={{ fontWeight: 700, transform: 'translate(0px, 25%)' }} >Procurar</Typography>
                    </Box>
                </MenuItem>
            </Link>
            <Link to='/dashboard' style={{ textDecoration: 'none', color: Cores.COR_SECUNDARIA_ESCURO }}>
                <MenuItem onClick={handleCloseMenu} sx={{ display: 'flex'}}>
                    <Box sx={{ display: 'flex' }}>
                        <IconButton color="secondary" component="label" >
                            <DashboardIcon fontSize='large' />
                        </IconButton>
                        <Typography sx={{ fontWeight: 700, transform: 'translate(0px, 33%)' }}>Minha Área</Typography>
                    </Box>
                </MenuItem>
            </Link>

            <Link to='/agendamento' style={{ textDecoration: 'none', color: Cores.COR_SECUNDARIA_ESCURO }}>
                <MenuItem onClick={handleCloseMenu}>
                    <Box sx={{ display: 'flex' }}>
                        <IconButton color="secondary" component="label" >
                            <DateRangeIcon fontSize='large' />
                        </IconButton>
                        <Typography sx={{ fontWeight: 700, transform: 'translate(0px, 33%)' }}>Agendamento</Typography>
                    </Box>
                </MenuItem>
            </Link>

            <Link to='/login' style={{ textDecoration: 'none', color: Cores.COR_SECUNDARIA_ESCURO }}>
                <MenuItem onClick={handleCloseMenu} sx={{ display: 'flex' }}>
                    <Box sx={{ display: 'flex', color: Cores.COR_SECUNDARIA_ESCURO }}>
                        <IconButton color="secondary" component="label">
                            <LoginIcon fontSize='large' />
                        </IconButton>
                        <Typography sx={{ fontWeight: 700, transform: 'translate(0px, 25%)' }} >Login</Typography>
                    </Box>
                </MenuItem>
            </Link>

            <MenuItem sx={{ display: 'none' }} onClick={() => setExibirModal(true)}>
                <Box sx={{ display: 'flex', color: Cores.COR_SECUNDARIA_ESCURO }}>
                    <IconButton color="secondary" component="label" >
                        <LogoutIcon fontSize='large' />
                    </IconButton>
                    <Typography sx={{ fontWeight: 700, transform: 'translate(0px, 25%)' }} >Logout</Typography>
                </Box>
            </MenuItem>

            <MenuItem onClick={() => { }}>
                <Typography>v{process.env.REACT_APP_VERSION}</Typography>
            </MenuItem>
        </Drawer>
    )
}