import { Box, Typography } from "@mui/material";
import { useState } from 'react';
import { useNavigate } from "react-router-dom";
import ClienteService from "../../api/services/ClienteService";
import { useCommon } from '../../components/hooks/useCommon';
import { default as mensagens } from "../../util/Mensagens";
import Utils from '../../util/Utils';
import constantes from "../../util/constantes";
import CadastroForm from "./cadastro/CadastroForm";
import Campos from './Campos';
import EsqueciSenhaForm from "./EsqueciSenhaForm";
import { useDados } from './hooks/useDados';
import logoImg from '../../img/logoMenor.png'
import * as Cores from '../../util/cores'

export default function Login(props) {
    const { setUserDetails, comecarCadastro, alterarLoad, notificar, Common, alterarMensagemLoad, invalidarUsuarioLogado } = props
    const navigate = useNavigate();
    const [esqueciSenha, setEsqueciSenha] = useState(false)
    const { values, handleInputChange, erros, validate, limparDados, mostraSenha, setMostraSenha } = useDados()
    const isNoite = Utils.verificarNoite()
    const [openPopupEsqueci, setOpenPopupEsqueci] = useState(false)
    const { height, width } = Utils.useWindowDimensions();
    const isMobile = width < constantes.MOBILE_SCREEN_SIZE

    const handleSubmit = async e => {
        e.preventDefault()
        if (validate() && !esqueciSenha) {
            navigator.geolocation.getCurrentPosition((position) => {
                realizarLogin({
                    username: values.username,
                    senha: values.senha,
                    latitude: position.coords.latitude,
                    longitude: position.coords.longitude
                })
            },
                function (error) {
                    if (error.code == error.PERMISSION_DENIED)
                        realizarLogin({
                            username: values.username,
                            senha: values.senha,
                            latitude: 0,
                            longitude: 0
                        })
                }
            )
        }
    }

    async function realizarLogin(credenciais) {
        alterarMensagemLoad(mensagens.FAZENDO_LOGIN)
        alterarLoad(true)
        try {
            await ClienteService.login(credenciais).then(response => {
                if (response.data.id > 0) {
                    setUserDetails({
                        roles: response.data.roles,
                        username: response.data.username,
                        id: response.data.id,
                        email: values.email,
                        token: response.data.token
                    })
                    navigate("/")
                } else if (response.data.id === 0) {
                    notificar(mensagens.ERRO_LOGIN_INVALIDO, "error")
                    invalidarUsuarioLogado()
                } else {
                    notificar(response.data.username, "error")
                    invalidarUsuarioLogado()
                }
            })
        } catch (e) {
            notificar("Erro ao realizar login. Tente novamente mais tarde.", "error")
            console.log(e)
        } finally {
            alterarLoad(false)
        }
    }


    return (
        <Box sx={{ height: '100%' }} id='login'>
            <Common />
            <Box sx={{ paddingTop: '35px' }} >
                <Typography variant='h5' fontWeight={700} color='white'>HealthConnect.com</Typography>
                <img style={{ borderRadius: '25px' }} src={logoImg} alt="Logo" height='150px' overflow='hidden' />
            </Box>
            <Box sx={{
                borderTopRightRadius: '15px', borderTopLeftRadius: '15px',
                background: Cores.COR_PRINCIPAL
            }}>
                <Box sx={{
                    borderRadius: '25px',
                    padding: isMobile ? '8%' : '5%',
                }}>
                    <Campos
                        handleSubmit={handleSubmit}
                        mostraSenha={mostraSenha}
                        setMostraSenha={setMostraSenha}
                        values={values}
                        erros={erros}
                        handleInputChange={handleInputChange}
                        comecarCadastro={comecarCadastro}
                    />
                </Box>

                <EsqueciSenhaForm
                    setOpenPopupEsqueci={setOpenPopupEsqueci}
                    openPopupEsqueci={openPopupEsqueci}
                    notificar={notificar}
                    alterarLoad={alterarLoad}
                />
            </Box>
        </Box>
    )
}