import { Box } from "@mui/material";
import HomeMobile from "./HomeMobile";
import HomeWeb from "./HomeWeb";

export default function HomeWrapper(props) {
    const { userDetails, isMobile } = props

    return (
        <Box sx={{height: '100%'}}>
            {isMobile ? (
                <HomeMobile
                    userDetails={userDetails}
                />
            ) : (
                <HomeWeb
                    userDetails={userDetails}
                />
            )
            }
        </Box>
    )
}