import { InputAdornment, TextField } from "@mui/material";
export default function Input(props) {

    const { name, label, value, error = null, onChange, inputPropText = '', background = 'white', disableUnderline = false, ...other } = props

    return (
        <TextField
            variant='filled'
            label={label}
            name={name}
            value={value}
            onChange={onChange}
            sx={{
                background: background, borderRadius: '50px',
                "& .MuiFormLabel-root": {
                    color: error === '' && 'black'
                    
                },
                '& .MuiFilledInput-root': {
                    backgroundColor: 'rgba(0, 0, 0, 0.00)'
                },
                '& .MuiFilledInput-root.Mui-focused': {
                    backgroundColor: 'rgba(0, 0, 0, 0.00)'
                }
            }}
            InputProps={{
                disableUnderline: disableUnderline, ...other.InputProps
            }}
            {...other}
            //{...(error && { error: true, helperText: error })}
        />
    )
}