import { Box, Stack, Typography } from "@mui/material";
import Controls from '../../../../components/controls/Controls'
import AddIcon from '@mui/icons-material/Add';
import { useState } from 'react'
import FormularioIncluirConsulta from "./FormularioIncluirConsulta";
import Tabela from "./Tabela";
import Filtros from "./Filtros";
import ModalVisualizarAgendamento from "./ModalVisualizarAgendamento";
import ModalCancelarAgendamento from "./ModalCancelarAgendamento";
import * as Cores from '../../../../util/cores'

export default function Agendamentos(props) {
    const [popupVisualizar, setPopupVisualizar] = useState(false)
    const [popupCancelar, setPopupCancelar] = useState(false)
    const [openPopupNovoAgendamento, setOpenPopupNovoAgendamento] = useState(0);
    const [registro, setRegistro] = useState({ username: '', email: '' })

    const abrirNovoAgendamento = () => {
        setOpenPopupNovoAgendamento(true)
    }

    const abrirPopupVisualizar = () => {
        setPopupVisualizar(true)
    }

    const abrirPopupCancelar = () => {
        setPopupCancelar(true)
    }

    return (
        <Box>
            <Box sx={{ padding: '10px' }}>
                <Stack spacing={1} padding={1}>
                    <Controls.Button
                        color='primary'
                        text={
                            <Box sx={{
                                display: 'flex'
                            }}>
                                <AddIcon sx={{fill: Cores.COR_PRINCIPAL_FUNDO}}/>
                                <Typography color='white' fontWeight={700}> Consulta</Typography>
                            </Box>
                        }
                        size='large'
                        onClick={() => { abrirNovoAgendamento() }}
                    />
                </Stack>
            </Box>

            <Filtros />

            <Box sx={{ width: '97%', marginLeft: '1.5%', overflowY: 'hide', overflowX: 'scroll' }}>
                <Tabela
                    abrirPopupVisualizar={abrirPopupVisualizar}
                    abrirPopupCancelar={abrirPopupCancelar}
                />
            </Box>

            <FormularioIncluirConsulta
                setOpenPopup={setOpenPopupNovoAgendamento}
                openPopup={openPopupNovoAgendamento}
            />

            <ModalVisualizarAgendamento
                openPopup={popupVisualizar}
                setOpenPopup={setPopupVisualizar}
                registro={registro}
            />

            <ModalCancelarAgendamento
                openPopup={popupCancelar}
                setOpenPopup={setPopupCancelar}
                registro={registro}
            />

        </Box>
    )
}