import { Box } from "@mui/material";
import { useState } from 'react';
import * as Constantes from '../../../../util/constantes';
import DataSelecionadaHeader from "./DataSelecionadaHeader";
import TabelaDias from "./TabelaDias";
import DiaMesAno from "./header-calendario/DiaMesAno";

export default function SelecionarData(props) {
    const { isMobile, setDataSelecionada, dataSelecionada, alterarCalendarioAgendamento, calendarioAgendamento } = props
    const [semanaMesAnoSelecionado, setSemanaMesAnoSelecionado] = useState(Constantes.SEMANA)
    const [diaMesSelecionado, setDiaMesSelecionado] = useState(null)


    const selecionarDiaMes = (value) => {
        setDiaMesSelecionado(value)
    }

    const selecionarSemanaMesAno = (value) => {
        setSemanaMesAnoSelecionado(value)
    }

    return (
        <Box sx={{ width: '100%' }}>
            <DiaMesAno
                selecionarSemanaMesAno={selecionarSemanaMesAno}
                semanaMesAnoSelecionado={semanaMesAnoSelecionado}
            />
            <TabelaDias
                selecionarDiaMes={selecionarDiaMes}
                diaMesSelecionado={diaMesSelecionado}
            />

        </ Box>
    )
}