import { Box, Divider, Typography } from "@mui/material";
import { useState } from 'react';

export default function PerfilOpcao(props) {
    const { titulo, selecionarOpcao } = props

    return (

        <Box sx={{ textAlign: 'left', paddingTop: '8px', paddingBottom: '8px' }} onClick={() => selecionarOpcao(titulo)}>
            <Typography>{titulo}</Typography>
            <Divider />
        </Box>

    )
}