import { Box, Typography } from "@mui/material";



export default function CampoSemInput(props){
    const {label, texto, area=false} = props
    return (

        <Box>
            <Typography fontWeight={700}>{label}</Typography>
            <Typography sx={{padding: area ? '5px' : '', borderRadius: area ? '5px' :'', background: area ? '#ebebeb': 'white', border:  area ? '1px solid grey' : ''}} 
            variant='body2'>{texto}</Typography>
        </Box>
    )
}