import { FormControl, FormHelperText, InputLabel, MenuItem, Select as MuiSelect } from "@mui/material";
import * as Cores from "../../util/cores";
import Utils from "../../util/Utils";


export default function Select(props) {

    const { name, label, value, error = null, onChange, options, sx, disableUnderline= false, ...other } = props;

    return (
        <FormControl variant='filled'
            sx={{ width: '100%', background: 'white', borderRadius: '50px' }}
            {...(error && { error: true })}>
            <InputLabel>{label}</InputLabel>
            <MuiSelect
                label={label}
                name={name}
                value={value}
                sx={{ background: 'white', borderRadius: '50px' }}
                onChange={onChange}
                disableUnderline={disableUnderline}
                MenuProps={{
                    PaperProps: {
                        sx: {
                            maxHeight: 400,
                            ...Utils.barra()

                        }
                    }
                }}
                {...other}>
                {
                    options.map(
                        item => (
                            <MenuItem key={item.id} value={item.id}>{item.value}</MenuItem>
                        )

                    )
                }
            </MuiSelect>
            {error && <FormHelperText>{error}</FormHelperText>}
        </FormControl>
    )
}

