
import { Box, Grid, Typography } from "@mui/material";
import { useState } from 'react';
import Controls from "../../../components/controls/Controls";
import * as Constantes from '../../../util/constantes';
import * as Cores from '../../../util/cores';
import CadastroClinica from './CadastroClinica';
import CadastroPaciente from './CadastroPaciente';

const initialValues = {
    id: 0,
    username: '',
    email: '',
    senha: '',
    confirmarSenha: '',
    check: false
}

export default function CadastroForm(props) {
    const { alterarLoad, notificar, sairCadastro } = props
    const [selecao, setSelecao] = useState('')

    const alterarSelecao = (valor) => {
        setSelecao(valor)
    }

    return (
        <Box sx={{ height: '100%',padding: '28px', background: Cores.COR_PRINCIPAL, borderTopRightRadius: '25px', borderTopLeftRadius: '25px', marginBottom: '64px' }}>
            {selecao === '' &&
                <Box sx={{height: '100%', paddingTop: '35%'}}>
                    <Box sx={{height: '60px',background: 'white'}} onClick={() => alterarSelecao(Constantes.CADASTRO_PACIENTE)}>
                        <Typography color={Cores.COR_PRINCIPAL} >Cadastrar como Paciente</Typography>
                    </Box>
                    <Box sx={{height: '60px',background: 'white', marginTop: '25px'}} onClick={() => alterarSelecao(Constantes.CADASTRO_CLINICA)}>
                        <Typography color={Cores.COR_PRINCIPAL} >Cadastrar como Profissional/Clínica</Typography>
                    </Box>
                    <Grid container>
                        <Grid item md={12} xs={12} sx={{paddingTop: '50px'}}>
                            <Controls.Button
                                sx={{ background: 'white', color: Cores.COR_PRINCIPAL, fontWeight: 700 }}
                                text='Voltar'
                                onClick={() => sairCadastro()}
                            />
                        </Grid>
                    </Grid>
                </Box>
            }
            {selecao === Constantes.CADASTRO_CLINICA &&
                <Box>
                    <CadastroClinica
                        alterarSelecao={alterarSelecao}
                    />
                </Box>
            }
            {selecao === Constantes.CADASTRO_PACIENTE &&
                <Box>
                    <CadastroPaciente
                        alterarSelecao={alterarSelecao}
                    />
                </Box>
            }
        </Box>

    )
}