import Controls from "../Controls";

const listaEspecialidade = [{ id: 0, value: 'Selecione' }, { id: 1, value: 'Ortopedista' }]

export default function Especialidade(props) {

    const { name, value, error = null, onChange, sx, ...other } = props;

    return (
        <Controls.Select
            error={error}
            name={name}
            value={value}
            label='Especialidade'
            options={listaEspecialidade}
            onChange={onChange}
        />
    )
}

