import { FormControl, FormControlLabel, FormHelperText, Checkbox as MuiCheckbox } from "@mui/material";

export default function Checkbox(props) {

    const { name, label, value, onChange, error, fontColor = 'black', ...other } = props

    const convertToDefEventParameter = (name, value) => ({
        target: {
            name,
            value
        }
    })

    return (
        <FormControl error={error !== ""}>
            <FormControlLabel
                label={label}
                sx={{ color: fontColor }}
                control={<MuiCheckbox
                    sx={{
                        color: fontColor,
                        "&.Mui-checked": {
                            color: fontColor,
                        }
                    }}
                    name={name}
                    checked={value}
                    onChange={e => { onChange(convertToDefEventParameter(name, e.target.checked)) }}
                    color='primary'
                    {...other}
                />}
            >

            </FormControlLabel>
            <FormHelperText>{error}</FormHelperText>
        </FormControl>
    )
}