import { Box } from "@mui/material";
import { useState } from 'react'
import StepperAgendamento from "../../../../components/stepper/agendamento/StepperAgendamento";
import { useCommon } from '../../../../components/hooks/useCommon';
import Popup from "../../../../components/Popup";
import Especialidade from "../../../../components/stepper/agendamento/Especialidade";
import EscolherProfissional from "../../../../components/stepper/agendamento/EscolherProfissional";
import Confirmacao from "../../../../components/stepper/agendamento/Confirmacao";


export default function FormularioIncluirConsulta(props) {
    const { openPopup, setOpenPopup } = props
    const [activeStep, setActiveStep] = useState(0);
    const [agendamento, setAgendamento] = useState({})
    const { Common, alterarLoad, alterarMensagemLoad, notificar } = useCommon()

    const fecharPopup = (valor) => {
        setOpenPopup(valor)
        setActiveStep(0)
    }

    const adicionarEspecialidade = (values) => { }
    const adicionarEsolherProfissional = (values) => { }


    const montarChildren = () => {
        return (
            <Box sx={{ marginTop: '5%', marginInline: '15px' }}>
                <Common />
                {activeStep === 0 &&
                    <Box>
                        <Especialidade
                            adicionarEspecialidade={adicionarEspecialidade}
                        />
                    </Box>}
                {activeStep === 1 &&
                    <Box>
                        <EscolherProfissional
                            adicionarEsolherProfissional={adicionarEsolherProfissional}
                        />
                    </Box>}
                {activeStep === 2 &&
                    <Box>
                        <Confirmacao />
                    </Box>}
            </Box>
        )
    }

    return (
        <Box>
            <Popup
                miniPadding={true}
                width='100%'
                openPopup={openPopup}
                setOpenPopup={() => fecharPopup()}
                title={'Novo Agendamento'}
                variant='body1'
            >
                <StepperAgendamento
                    fecharPopup={fecharPopup}
                    activeStep={activeStep}
                    setActiveStep={setActiveStep}
                    children={montarChildren()}
                />
            </Popup>
        </Box>
    )
}