import { Box, Grid, Typography } from "@mui/material"
import Controls from "../../controls/Controls"
import { Form, useForm } from "../../UseForm"
import * as Cores from '../../../util/cores'

const initialValues = {
    profissional: 0,
    horario: 0,
    data: 1,
    check: false
}

export default function EscolherProfissional(props) {
    const {adicionarEsolherProfissional} = props


    const validate = () => {

    }

    const {
        values,
        setValues,
        handleInputChange,
        erros,
        setErros
    } = useForm(initialValues, true, validate)

    const handleSubmit = () => {

    }

    return (
        <Box>
            <Form onSubmit={handleSubmit} >
                <Grid container spacing={3} >
                    <Grid item md={12} xs={12}>
                        <Controls.Input.Profissional
                            name='profissional'
                            value={values.profissional}
                            error={erros.profissional}
                            onChange={handleInputChange}
                        />
                    </Grid>
                    <Grid item md={12} xs={12}>
                        <Controls.Checkbox
                            name="check"
                            fontColor={Cores.COR_PRINCIPAL}
                            label="Qualquer profissional disponível"
                            value={values.check}
                            error={erros.check}
                            onChange={handleInputChange}
                        />
                    </Grid>
                    <Grid item md={12} xs={12}>
                        <Controls.TextField
                            name='data'
                            label='Data'
                            value={values.data}
                            error={erros.data}
                            onChange={handleInputChange}
                        />
                    </Grid>
                    <Grid item md={12} xs={12}>
                        <Controls.TextField
                            name='horario'
                            label='Horário'
                            value={values.horario}
                            error={erros.horario}
                            onChange={handleInputChange}
                        />
                    </Grid>
                </Grid >
            </Form>
        </Box>
    )
}