import { Box } from "@mui/material";
import { useEffect } from "react";
import { useCommon } from "../../components/hooks/useCommon";
import Utils from "../../util/Utils";
import * as Cores from "../../util/cores";
import constantes from "../../util/constantes";
import CardMinhaArea from "./CardMinhaArea";
import DateRangeIcon from '@mui/icons-material/DateRange';
import PersonIcon from '@mui/icons-material/Person';
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import PaymentsIcon from '@mui/icons-material/Payments';
import GroupsIcon from '@mui/icons-material/Groups';
import LocalHospitalIcon from '@mui/icons-material/LocalHospital';

const itensTela = [
    [{ titulo: 'Conta', icone: <PersonIcon fontSize='large' />, link: '/perfil' }, 
    { titulo: 'Agendamentos', icone: <DateRangeIcon fontSize='large' />, link: '/agendamento' }],
    [{ titulo: 'Clínica', icone: <LocalHospitalIcon fontSize='large' />, link: '/clinica' },
    { titulo: 'Financeiro', icone: <PaymentsIcon fontSize='large' />, link: '/financeiro' }],

    [{ titulo: 'Profissionais', icone: <GroupsIcon fontSize='large' />, link: '/profissionais' }]

]

export default function Dashboard(props) {
    const { alterarLoad, Common, notificar } = useCommon()
    const { userDetails } = props

    const { height, width } = Utils.useWindowDimensions();
    const isMobile = width < constantes.MOBILE_SCREEN_SIZE

    useEffect(() => {
    }, [])


    return (
        <Box sx={{ width: '100%', padding: isMobile ? '8px' : '15px', background: Cores.COR_PRINCIPAL_FUNDO, height: '100%' }}>
            <Common />
            <Box sx={{height: '100%'}}>
                {itensTela.map((itemLinha, indexItem) => {
                    return <Box key={indexItem} sx={{display: 'flex', width: '100%', height: '20%'}}>
                        {itemLinha.map((item, index) => {
                            return (
                                <Box key={index} sx={{width: '48%', padding: '1% 1%'}}>
                                    <CardMinhaArea
                                        icone={item.icone}
                                        titulo={item.titulo}
                                        link={item.link}
                                    />
                                </Box>
                            )
                        })}
                    </Box>

                })}
            </Box>
        </Box>)
}