import DashboardIcon from '@mui/icons-material/Dashboard';
import DateRangeIcon from '@mui/icons-material/DateRange';
import LoginIcon from '@mui/icons-material/Login';
import SearchIcon from '@mui/icons-material/Search';
import { IconButton, MenuItem, Typography } from '@mui/material';
import Box from '@mui/material/Box';
import { Link } from 'react-router-dom';
import * as Cores from '../../util/cores';
import * as Utils from '../../util/Utils';
import * as Constantes from '../../util/constantes';
import LinhaEmbaixoMenu from './LinhaEmbaixoMenu';

export default function MenuMobile(props) {

    const { userDetails, handleCloseMenu, corpo } = props

    return (
        <Box sx={{
            background: 'white', display: 'flex', borderTopLeftRadius: '25px', borderTopRightRadius: '25px', justifyContent: 'center',
            boxShadow: '0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)'
        }}>
            <Link to='/' style={{ width: '33%', textDecoration: 'none', color: Cores.COR_PRINCIPAL_FOSCO }}>
                <MenuItem onClick={handleCloseMenu} sx={{ display: 'grid', justifyContent: 'center' }}>
                    <IconButton component="label" sx={{ padding: '0px' }}>
                        <SearchIcon fontSize='small' sx={{ fill: Cores.COR_PRINCIPAL_FOSCO }} />
                    </IconButton>
                    <Typography sx={{ fontWeight: 700, transform: 'translate(0px, 25%)' }} variant='caption' >Procurar</Typography>
                    {corpo === Constantes.PAGE_HOME &&
                        <LinhaEmbaixoMenu />
                    }
                </MenuItem>
            </Link>
            {Utils.verificarUsuarioLogado(userDetails) &&
                <Link to={'/' + Constantes.PAGE_AGENDAMENTO} style={{ width: '33%', textDecoration: 'none', color: Cores.COR_PRINCIPAL_FOSCO }}>
                    <MenuItem onClick={handleCloseMenu} sx={{ display: 'grid', justifyContent: 'center' }}>
                        <IconButton color="secondary" component="label" sx={{ padding: '0px' }}>
                            <DateRangeIcon fontSize='small' sx={{ fill: Cores.COR_PRINCIPAL_FOSCO }} />
                        </IconButton>
                        <Typography sx={{ fontWeight: 700, transform: 'translate(0px, 33%)' }} variant='caption'>Agendamento</Typography>
                        {corpo === Constantes.PAGE_AGENDAMENTO &&
                            <LinhaEmbaixoMenu />
                        }
                    </MenuItem>
                </Link>
            }
            {Utils.verificarUsuarioLogado(userDetails) ?
                <Link to={'/' + Constantes.PAGE_DASHBOARD} style={{ width: '33%', textDecoration: 'none', color: Cores.COR_PRINCIPAL_FOSCO }}>
                    <MenuItem onClick={handleCloseMenu} sx={{ display: 'grid', justifyContent: 'center' }}>
                        <IconButton color="secondary" component="label" sx={{ padding: '0px' }}>
                            <DashboardIcon fontSize='small' sx={{ fill: Cores.COR_PRINCIPAL_FOSCO }} />
                        </IconButton>
                        <Typography sx={{ fontWeight: 700, transform: 'translate(0px, 33%)' }} variant='caption'>Minha Área</Typography>
                        {corpo === Constantes.PAGE_DASHBOARD &&
                            <LinhaEmbaixoMenu />
                        }
                    </MenuItem>
                </Link>
                :
                <Link to={'/' + Constantes.PAGE_LOGIN} style={{ width: '33%', textDecoration: 'none', color: Cores.COR_PRINCIPAL_FOSCO }}>
                    <MenuItem onClick={handleCloseMenu} sx={{ display: 'grid', justifyContent: 'center' }}>
                        <IconButton color="secondary" component="label" sx={{ padding: '0px' }}>
                            <LoginIcon fontSize='small' sx={{ fill: Cores.COR_PRINCIPAL_FOSCO }} />
                        </IconButton>
                        <Typography sx={{ fontWeight: 700, transform: 'translate(0px, 33%)' }} variant='caption' >Entrar</Typography>
                        {corpo === Constantes.PAGE_LOGIN &&
                            <LinhaEmbaixoMenu />
                        }
                    </MenuItem>
                </Link>
            }
        </Box>
    )
}
