import { Box, Typography } from "@mui/material";
import { useState } from 'react';
import { useCommon } from "../../components/hooks/useCommon";
import * as Utils from "../../util/Utils";
import Login from "./Login";
import CadastroForm from "./cadastro/CadastroForm";

export default function LoginWrapper(props) {
    const { invalidarUsuarioLogado, setUserDetails, userDetails, isMobile } = props
    const [isCadastro, setIsCadastro] = useState(false)
    const { alterarLoad, notificar, Common, alterarMensagemLoad } = useCommon()

    const comecarCadastro = () => {
        setIsCadastro(true)
    }

    const sairCadastro = () => {
        setIsCadastro(false)
    }
    return (
        <Box sx={{ height: '100%' }}>
            {Utils.verificarUsuarioLogado(userDetails) ?
                <Typography>Usuário já logado!</Typography>
                :
                <Box sx={{height: '100%', overflowY: 'scroll'}}>
                    {!isCadastro ?
                        <Login
                            comecarCadastro={comecarCadastro}
                            isMobile={isMobile}
                            invalidarUsuarioLogado={invalidarUsuarioLogado}
                            setUserDetails={setUserDetails}
                            alterarLoad={alterarLoad}
                            notificar={notificar}
                            Common={Common}
                            alterarMensagemLoad={alterarMensagemLoad}
                        />
                        :
                        <Box sx={{paddingTop: '64px', height: '100%'}}>
                            <CadastroForm
                                sairCadastro={sairCadastro}
                                alterarLoad={alterarLoad}
                                notificar={notificar}
                            />
                        </Box>
                    }
                </Box>
            }
        </Box>
    )
}