import { Box } from "@mui/material";
import * as Constantes from '../../../../../util/constantes';
import * as Cores from '../../../../../util/cores';
import SelecionarDiaMesAno from "./SelecinarDiaMesAno";

export default function DiaMesAno(props) {
    const { selecionarSemanaMesAno, semanaMesAnoSelecionado } = props

    return (
        <Box sx={{padding: '6px 4px 6px 4px', display: 'flex', justifyContent: 'space-around', width: '100%'}}>
                <SelecionarDiaMesAno
                    selecionarSemanaMesAno={selecionarSemanaMesAno}
                    periodo={Constantes.SEMANA}
                    semanaMesAnoSelecionado={semanaMesAnoSelecionado}
                />
                 <SelecionarDiaMesAno
                    selecionarSemanaMesAno={selecionarSemanaMesAno}
                    periodo={Constantes.MES}
                    semanaMesAnoSelecionado={semanaMesAnoSelecionado}
                />
                 <SelecionarDiaMesAno
                    selecionarSemanaMesAno={selecionarSemanaMesAno}
                    periodo={Constantes.ANO}
                    semanaMesAnoSelecionado={semanaMesAnoSelecionado}
                />
        </ Box>
    )
}