
import { Box, Paper } from "@mui/material";
import { useNavigate } from "react-router-dom";
import Controls from "../../components/controls/Controls";
import Mensagens from "../../util/Mensagens";
import * as Cores from "../../util/cores";
import * as Icones from '../../util/icones';

export default function HomeWeb(props) {
    const navigate = useNavigate();

    const handleRedirectLogin = () => {
        navigate("/login")
    }
    return (
        <Box>
            <Paper sx={{ background: Cores.COR_SECUNDARIA_FUNDO }}>
                <Box sx={{ paddingBottom: '10px' }}>
                    <Controls.Button
                        startIcon={Icones.loginSecundario}
                        text={Mensagens.LOGIN}
                        onClick={() => handleRedirectLogin()}
                        variant='outlined'
                        color='secondary'
                        sx={{ width: '50%', marginTop: '25px', fontSize: '25px', background: 'white' }}
                    />
                </Box>
            </Paper>
        </Box>
    )
}