import { Box, Grid, Typography } from "@mui/material";
import { useState } from 'react';
import ClienteService from "../../../api/services/ClienteService";
import ModalConfirmacao from "../../../components/ModalConfirmacao";
import Notificacao from "../../../components/Notificacao";
import { Form, useForm } from "../../../components/UseForm";
import Controls from "../../../components/controls/Controls";
import Mensagens from "../../../util/Mensagens";
import * as Cores from "../../../util/cores";
import PerfilOpcao from "./PerfilOpcao";
import * as Constantes from '../../../util/constantes'

export default function PerfilOpcoes(props) {
    const { selecionarOpcao } = props

    const [exibirModal, setExibirModal] = useState(false)

    return (

        <Box sx={{ textAlign: 'left' }}>
            <PerfilOpcao
                selecionarOpcao={selecionarOpcao}
                titulo={Constantes.PERFIL_OPCOES_DADOS_CONTA}
            />
            <PerfilOpcao
                selecionarOpcao={selecionarOpcao}
                titulo={Constantes.PERFIL_OPCOES_MINHAS_CONSULTAS}
            />
            <PerfilOpcao
                selecionarOpcao={selecionarOpcao}
                titulo={Constantes.PERFIL_OPCOES_LOGOUT}
            />
        </Box>

    )
}