import { Box, Grid } from "@mui/material";
import Controls from "../../../../components/controls/Controls";
import Popup from "../../../../components/Popup";
import { Form, useForm } from "../../../../components/UseForm";

const initialValues = {

}

export default function ModalVisualizarAgendamento(props) {
    const { openPopup, setOpenPopup, registro } = props

    const fecharPopup = () => {
        setOpenPopup(false)
    }

    const handleSubmit = () => {

    }

    return (
        <Popup
            width='95%'
            openPopup={openPopup}
            setOpenPopup={setOpenPopup}
            title={'Imprimir Agendamento'}
            variant='body1'
        >
            <Form onSubmit={handleSubmit}>
                <Grid container spacing={2}>
                    <Grid item xs={12} md={6}>
                        <Controls.CampoSemInput
                            label={'Paciente'}
                            texto={'BERNARDO JOSE ALVES SANTANA'}
                        />
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <Controls.CampoSemInput
                            label={'Convênio/Plano de Saúde'}
                            texto={'CAMED - Caixa de assistência dos Funcionários...'}
                        />
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <Controls.CampoSemInput
                            label={'Data'}
                            texto={'01/03/2024 às 09:30'}
                        />
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <Controls.CampoSemInput
                            label={'Unidade'}
                            texto={'CLINICAMED FORTALEZA/CE'}
                        />
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <Controls.CampoSemInput
                            label={'Endereço'}
                            texto={'Av. Santos Dumont, 782 - Centro'}
                        />
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <Controls.CampoSemInput
                            label={'Telefone da unidade'}
                            texto={'(85) 3533-4854'}
                        />
                    </Grid>
                    <Grid item xs={12} md={12}>
                        <Controls.CampoSemInput
                            label={'Instruções'}
                            texto={'Chegar com 30 minutos de antecedência'}
                            area={true}
                        />
                    </Grid>
                    <Grid item xs={12} md={12}>
                        <Controls.CampoSemInput
                            label={'Recomendações'}
                            texto={'Especialidade destinada para crianças de até 15 anos.'}
                            area={true}
                        />
                    </Grid>

                    <Grid item xs={6}>
                        <Controls.Button
                            text='Imprimir'
                            type='submit'
                        />
                    </Grid>
                    <Grid item xs={6}>
                        <Controls.Button
                            text='Fechar'
                            onClick={() => fecharPopup()}
                        />
                    </Grid>
                </Grid>
            </Form>
        </Popup>
    )
}