import { Box, Divider, Typography } from "@mui/material";
import { useEffect, useState } from 'react';
import ClienteService from '../../api/services/ClienteService';
import { useCommon } from '../../components/hooks/useCommon';
import Utils from '../../util/Utils';
import constantes from '../../util/constantes';
import * as Constantes from '../../util/constantes';
import * as Cores from '../../util/cores'
import GerenciarConta from './GerenciarConta';
import { useNavigate } from "react-router-dom";
import PerfilOpcoes from "./perfil-corpo/PerfilOpcoes";
import Dashboard from "../dashboard/Dashboard";
import MinhasConsultas from "./minhas-consultas/MinhasConsultas";
import MeusDados from "./meus-dados/MeusDados";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';

export default function PerfilWrapper(props) {
    const { userDetails, setUserDetails, isMobile } = props
    const { notificar, alterarLoad, alterarMensagemLoad, Common } = useCommon()
    const [corpo, setCorpo] = useState(constantes.CONTA_MENU)
    const [opcaoSelecionada, setOpcaoSelecionada] = useState('')

    const navigate = useNavigate();

    const handleRedirectLogin = () => {
        navigate("/login")
    }

    const selecionarOpcao = (valor) => {
        if (valor === Constantes.PERFIL_OPCOES_LOGOUT) {
            setUserDetails({
                roles: '',
                username: '',
                id: 0,
                email: '',
                senha: ''
            })
        }
        setOpcaoSelecionada(valor)

    }

    useEffect(() => {
        consultarCliente(userDetails.id)
    }, []);

    async function consultarCliente(codigo) {
        await ClienteService.consultarCliente(codigo).then((response) => {
        })
    }

    return (
        <Box sx={{ height: '100%', paddingTop: '64px' }}>
            <Common />
            {opcaoSelecionada !== '' && userDetails.id !== 0 &&
                <Box sx={{display: 'flex', background: 'white'}}>
                    <ArrowBackIcon />
                    <Typography onClick={() => selecionarOpcao('')} sx={{ paddingTop: '5px', background: 'white', borderTopLeftRadius: '25px', borderTopRightRadius: '25px' }}>
                        Voltar
                    </Typography>
                </Box>
            }

            {userDetails === null || userDetails.id === 0 ?
                <Box sx={{ height: '100px', background: 'white', borderRadius: '25px' }} onClick={() => handleRedirectLogin()}>
                    Clique aqui e faça o Login!
                </Box>
                :
                <Box sx={{ width: '100%', ...Utils.barra(), height: '100%', background: 'white', padding: '16px' }}>

                    <Box>
                        {opcaoSelecionada === '' &&
                            <PerfilOpcoes
                                selecionarOpcao={selecionarOpcao}
                            />
                        }
                        {opcaoSelecionada === Constantes.PERFIL_OPCOES_DADOS_CONTA &&
                            <MeusDados />
                        }

                        {opcaoSelecionada === Constantes.PERFIL_OPCOES_MINHAS_CONSULTAS &&
                            <MinhasConsultas />
                        }
                        {opcaoSelecionada === Constantes.PERFIL_OPCOES_LOGOUT &&
                            < Typography > Saiu!</Typography>
                        }
                    </Box>
                </Box>
            }
        </Box >
    )
}