import { Dialog, DialogContent, DialogTitle, Divider, Typography } from "@mui/material"
import Controls from "./controls/Controls"
import CloseIcon from '@mui/icons-material/Close'
import * as Cores from "../util/cores"
import Utils from '../util/Utils'
export default function Popup(props) {
    const { title, children, openPopup, setOpenPopup, maxWidth = 'lg', width = '40%', variant = 'h6', miniPadding=false } = props

    return (
        <Dialog
            sx={{
                '& .MuiPaper-root': {
                    borderRadius: '12px',
                    margin: '8px',
                    width: width,
                    maxHeight: '75%'
                }
            }}
            open={openPopup}
            fullWidth={true}
            onClose={() => setOpenPopup(false)}
            maxWidth={maxWidth} 
            >
                
            <DialogTitle
                sx={{padding: '8px 10px'}}
            >
                <div style={{ display: 'flex' }}>
                    <Typography variant={variant} component='span' sx={{ marginTop: '5px', flexGrow: '1', fontWeight: 700 }} color='primary'> {title} </Typography>
                    <Controls.ActionButton
                        color='secondary'
                        onClick={() => { setOpenPopup(false) }}>
                        <CloseIcon fontSize='small'/>
                    </Controls.ActionButton>
                </div>
            </DialogTitle>
            <Divider />
            <DialogContent
                sx={{...Utils.barra(), padding: miniPadding ? '6px 8px ' : '20px 24px'}}
            >
                {children}
            </DialogContent>
        </Dialog>
    )
}


