import { Box, Grid, Typography } from "@mui/material";
import Controls from "../../../../components/controls/Controls";
import Popup from "../../../../components/Popup";
import { Form, useForm } from "../../../../components/UseForm";

const initialValues = {
    motivo: '',
    observacao: ''
}

export default function ModalCancelarAgendamento(props) {
    const { openPopup, setOpenPopup, registro } = props
    
    const fecharPopup = () => {
        setOpenPopup(false)
    }

    const validate = () => { }


    const {
        values,
        setValues,
        handleInputChange,
        erros,
        setErros
    } = useForm(initialValues, true, validate)

    const handleSubmit = () => {

    }

    return (

        <Popup
            width='100%'
            openPopup={openPopup}
            setOpenPopup={setOpenPopup}
            title={'Cancelar Agendamento'}
            variant='body1'
        >
            <Form onSubmit={handleSubmit}>
                <Typography>Gostaria de desmarcar esse agendamento? <b>Essa ação não pode ser desfeita!</b></Typography>
                <Grid container spacing={1}>
                    <Grid item xs={12} md={12}>
                        <Controls.TextField
                            name='motivo'
                            label='Motivo'
                            onChange={handleInputChange}
                            value={values.motivo}
                            error={erros.motivo}
                        />
                    </Grid>
                    <Grid item xs={12} md={12}>
                        <Controls.TextField
                            name='observacao'
                            label='Observação'
                            onChange={handleInputChange}
                            value={values.observacao}
                            error={erros.observacao}
                        />
                    </Grid>

                    <Grid item xs={6}>
                        <Controls.Button
                            text='Confirmar'
                            type='submit'
                        />
                    </Grid>

                    <Grid item xs={6}>
                        <Controls.Button
                            text='Fechar'
                            onClick={() => fecharPopup()}
                        />

                    </Grid>
                </Grid>
            </Form>
        </Popup>
    )
}