import VisibilityIcon from '@mui/icons-material/Visibility';
import { Grid, IconButton, InputAdornment, Typography } from "@mui/material";
import { Form } from '../../components/UseForm';
import Controls from "../../components/controls/Controls";
import * as Cores from '../../util/cores'

export default function Campos(props) {

    const { values, erros, handleInputChange, mostraSenha, setMostraSenha, handleSubmit, comecarCadastro, setOpenPopupEsqueci } = props

    const handleClickShowPassword = (e) => {
        e.preventDefault()
        if (mostraSenha === 'password') {
            setMostraSenha('text')
        } else {
            setMostraSenha('password')
        }
    }

    return (
        <Form onSubmit={handleSubmit} >
            <Grid container spacing={3} >
                <Grid item md={12} xs={12}>
                    <Controls.TextField
                        name='username'
                        label='Username'
                        value={values.username}
                        error={erros.username}
                        onChange={handleInputChange}
                        disableUnderline={true}
                    />
                </Grid>
                <Grid item md={12} xs={12}>
                    <Controls.TextField
                        name='senha'
                        label='Senha'
                        value={values.senha}
                        error={erros.senha}
                        onChange={handleInputChange}
                        type={mostraSenha}
                        InputProps={{
                            disableUnderline: true,
                            endAdornment: (
                                <InputAdornment position="end">
                                    <IconButton
                                        aria-label="toggle password visibility"
                                        onClick={(e) => handleClickShowPassword(e)}
                                        sx={{
                                            '& .MuiSvgIcon-root': {
                                                fill:'black',
                                                '&:hover': {
                                                    cursor: 'pointer',
                                                }
                                            }
                                        }}
                                    >
                                        <VisibilityIcon />
                                    </IconButton>
                                </InputAdornment>
                            )
                        }}
                    />
                </Grid>
                <Grid item md={12} xs={12}>
                    <Controls.Button
                        type="submit"
                        text='Log in'
                        sx={{background: 'white', fontWeight: 700, color: Cores.COR_PRINCIPAL}}
                    />
                    <Controls.Button
                        sx={{ marginLeft: '10px', background: 'white', fontWeight: 700, color: Cores.COR_PRINCIPAL}}
                        text='Criar conta'
                        onClick={() => { comecarCadastro() }}
                    />
                </Grid>
                <Grid item md={12} xs={12}>
                    <Typography color='white' onClick={() => setOpenPopupEsqueci(true)} fontWeight={600} sx={{ cursor: 'pointer' }} component='span'>
                        Esqueci minha senha
                    </Typography>
                </Grid>
            </Grid >
        </Form>
    )
}