import { useEffect, useState } from 'react';
import secureLocalStorage from "react-secure-storage";
import * as Cores from './cores';

const getAuth = () => {
    const userDetails = secureLocalStorage.getItem('userDetails')
    let headers =  {
        headers: {
            "Content-Type": "application/json",
            "Authorization": 'Bearer ' + userDetails.token
        }
    }
    return (headers)
}

const randomNumber = (min, max, precision) => {
    return Math.floor((min + Math.random() * (max - min)) * precision) / precision
}
export function verificarNoite() {
    const hours = new Date().getHours()
    const isDayTime = hours > 6 && hours < 19;
    if (isDayTime === true) {
        return false
    }
    return true
}
function dividirArrayEmGrupos(arr, len) {
    if (arr === null || arr === undefined) return []
    var chunks = [], i = 0, n = arr.length;
    while (i < n) {
        chunks.push(arr.slice(i, i += len));
    }
    return chunks;
}

function getWindowDimensions() {
    const { innerWidth: width, innerHeight: height } = window;
    return {
        width,
        height
    };
}

function useWindowDimensions() {
    const [windowDimensions, setWindowDimensions] = useState(getWindowDimensions());

    useEffect(() => {
        function handleResize() {
            setWindowDimensions(getWindowDimensions());
        }

        window.addEventListener('resize', handleResize);
        return () => window.removeEventListener('resize', handleResize);
    }, []);
    return windowDimensions;
}

export const verificarUsuarioLogado = (userDetails) => {
    if(userDetails === null || userDetails === undefined || userDetails.id === 0){
        return false
    }

    return true
}

const barra = () => {
    return {
        
        "::-webkit-scrollbar": {
            width: "6px",
        },
        "::-webkit-scrollbar-track": {
            backgroundColor: '#fff5f5'
        },
        "::-webkit-scrollbar-thumb": {
            background: Cores.COR_PRINCIPAL,
            borderRadius: "15px",
            height: "2px"
        },
        "::-webkit-scrollbar-thumb:hover": {
            background: Cores.COR_PRINCIPAL,
            maxHeight: "10px"
        }
    
    }
}

const Utils = {
    randomNumber,
    dividirArrayEmGrupos,
    useWindowDimensions,
    verificarNoite,
    barra,
    getAuth
}



export default Utils