import { Box, Grid } from "@mui/material";
import { Form, useForm } from "../../../../components/UseForm";
import Controls from "../../../../components/controls/Controls";

const initialValues = {
    dataInicio: '',
    dataFim: '',
    tipoBeneficiario: '',
    status: 0,
    nomePaciente: '',
}

const listaStatus = [{id: 0, value: 'Selecione'}, {id: 1, value: 'Pendente confirmação'}, {id: 2, value: 'Agendado'}, {id: 3, value: 'Concluído'}, {id: 4, value: 'Cancelado'}]
export default function Filtros(props) {

    const validate = () => {

    }

    const {
        values,
        setValues,
        handleInputChange,
        erros,
        setErros
    } = useForm(initialValues, true, validate)

    const handleSubmit = () => {

    }

    return (
        <Box sx={{padding: '0px 20px 20px 20px'}}>
            <Form onSubmit={handleSubmit} >
                <Grid container spacing={3} >
                    <Grid item md={6} xs={6}>
                        <Controls.TextField
                            name='dataInicio'
                            label='Data início'
                            value={values.dataInicio}
                            error={erros.dataInicio}
                            onChange={handleInputChange}
                        />
                    </Grid>
                    <Grid item md={6} xs={6}>
                        <Controls.TextField
                            name='dataFim'
                            label='Data Fim'
                            value={values.dataFim}
                            error={erros.dataFim}
                            onChange={handleInputChange}
                        />
                    </Grid>
                    <Grid item md={12} xs={12}>
                        <Controls.Select
                            name='status'
                            label='Status'
                            value={values.status}
                            error={erros.status}
                            onChange={handleInputChange}
                            options={listaStatus}
                        />
                    </Grid>
                    <Grid item md={12} xs={12}>
                        <Controls.TextField
                            name='tipoBeneficiario'
                            label='Tipo beneficiário'
                            value={values.tipoBeneficiario}
                            error={erros.tipoBeneficiario}
                            onChange={handleInputChange}
                        />
                    </Grid>
                </Grid >
            </Form>
        </Box>
    )
}